import React from 'react';
class TodoApp extends React.Component {
    constructor(props) {
      super(props);
      this.state = { items: [], text: '' ,done: []};
      this.handleChange = this.handleChange.bind(this);
      this.handleSubmit = this.handleSubmit.bind(this);
    }
  
    render() {
      return (
        <div className="container">
          <h3>TODO</h3>
          <form onSubmit={this.handleSubmit} className="row">
            <label htmlFor="new-todo" className="col-12">
              Enter Todos?
             </label>
            <input
              id="new-todo" className="form-control col-md-2 col-6  ml-3"
              onChange={this.handleChange}
              value={this.state.text}
            />
            <button className="btn btn-primary ml-2">
              Add #{this.state.items.length + 1}
            </button>
            {this.state.items.length > 0 &&  <button className="btn btn-warning ml-2  text-right"  onClick={this.clear}>clear</button> }
          </form>
            <div className="row">
                <div className="mt-3 col-12 col-md-4 pt-3 border-top">
                  <h4 className="mt-3 mb-3 text-danger">Tasks<span className="badge badge-danger ml-2">{this.state.items.length}</span></h4>
                 {this.state.items.length == 0 && <p>No Tasks</p>}
                 {this.state.items.slice(0, 5).map( item => (
                  <div className="card mb-2">
                         <div className="card-body d-flex justify-content-between align-items-center" >
                             <p className="m-0">{item.text}</p>
                            <div>
                               <button className="btn btn-primary ml-2 btn-sm" onClick={ () => this.doneTodo(item.text)}>Done</button>
                                <button className= "btn btn-success ml-2 btn-sm" disabled={(this.state.text.length > 0 && "true")} onClick={ () => this.editTodo(item.text)}>Edit</button>                             
                            </div>                          
                         </div>
                   </div>
                  ))}
                   <div className="col-12 text-center">
                       {this.state.items.length > 5 && <h5 className="text-primary mx-auto">more</h5>}
                   </div>
                  
                   <h4 className="mt-5 mb-3 text-success">Completed {this.state.done.length > 1 && <button className="btn btn-warning ml-2 btn-sm float-right"  onClick={ () => this.deleteallTodo()}>Delete All</button>}</h4>
                   {this.state.done.length == 0 && <p>No Completed Tasks</p>}
                  {this.state.done.map( done => (
                  <div className="mb-2">
                         <div className="d-flex justify-content-between" >
                             <p className="text-muted"><del> {done}</del></p>
                            <div>
                               <button className="btn btn-danger ml-2 btn-sm"  onClick={ () => this.deleteTodo(done)}>Delete</button>                            
                            </div>                          
                         </div>
                   </div>
                  ))}
               </div>
            </div>
             
        
        </div>
      );
    }
  
    handleChange(e) {
      this.setState({ text: e.target.value });
    }
     
     doneTodo = (itemvalue) =>{
       this.setState(state => ({
        items: state.items.filter( item => item.text !== itemvalue),
        done: state.done.concat(itemvalue),
      }));
     } 
     
     deleteallTodo = (itemvalue) =>{
       this.setState(state => ({
        done: []
      }));
     } 
     
     deleteTodo = (itemvalue) =>{
       this.setState(state => ({
        done: state.done.filter( done => done !== itemvalue)
      }));
     } 
     
     editTodo = (itemvalue) =>{
       this.setState(state => ({
        items: state.items.filter( item => item.text !== itemvalue),
        text:itemvalue
      }));
     } 
     clear= () => {
       this.setState(state => ({
        items: [],
        done:[]
      }));
     }
    handleSubmit(e) {
      e.preventDefault();
      if (!this.state.text.length) {
        return;
      }
      const newItem = {
        text: this.state.text
      };
      this.setState(state => ({
        items: state.items.concat(newItem),
        text: ''
      }));
    }
  }
  export default TodoApp;
 